/* apply a natural box layout model to all elements, but allowing components to change */
html {
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul, li {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
html,
body {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

:root {
  --telemetryBackground: rgba(10, 99, 164, 0.85);
  --telemetryBackgroundOpaque: rgb(55, 134, 196);
  --telemetryBackgroundDark: rgb(4, 99, 166, 0.8);
  --telemetryBackgroundDisabled: rgba(10, 99, 164, 0.25);
}

.App {
  text-align: center;
  background: -webkit-radial-gradient(circle, #c3e8ff 0%, #5486b4 100%, #171947 100%);
  background: radial-gradient(circle, #c3e8ff 0%, #5486b4 100%, #171947 100%);
}
.App .render-container {
  display: flex;
}
.App .render-container canvas {
  height: 100vh !important;
  width: 100vw !important;
}

.Telemetry {
  position: absolute;
  top: 15px;
  right: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 34px;
  color: white;
  background: -webkit-linear-gradient(left, var(--telemetryBackgroundDark), var(--telemetryBackground));
  background: linear-gradient(to right, var(--telemetryBackgroundDark), var(--telemetryBackground));
  width: 250px;
}

.Telemetry .skew {
  height: 34px;
  width: 40px;
  margin-left: -10px;
  box-shadow: 8px 0 10px -4px rgb(6, 55, 116);
  background: var(--telemetryBackgroundOpaque);
  -webkit-transform: skew(30deg);
          transform: skew(30deg);
  flex: 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: -webkit-linear-gradient(
    top left,
    var(--telemetryBackgroundOpaque),
    var(--telemetryBackgroundOpaque) 65%,
    var(--telemetryBackground)
  );
  background: linear-gradient(
    to bottom right,
    var(--telemetryBackgroundOpaque),
    var(--telemetryBackgroundOpaque) 65%,
    var(--telemetryBackground)
  );
}
.Telemetry .skew span {
  -webkit-transform: skew(-30deg);
          transform: skew(-30deg);
}

.Telemetry .time {
  flex: 0 1 100%;
  text-align: left;
  padding-left: 20px;
}

.ToggleIss {
  position: absolute;
  top: 15px;
  left: 15px;
  padding: 7px 15px;
  background: var(--telemetryBackgroundDisabled);
  color: #99c4e3;
  -webkit-transition: 400ms all;
  transition: 400ms all;
  cursor: pointer;
}

.ToggleIss.tracking {
  background: var(--telemetryBackground);
  color: white;
}

.Mission {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  color: white;
}

.Mission .name {
  display: flex;
  align-items: center;
  justify-content: center;
  background: black;
  -webkit-transform: skew(30deg);
          transform: skew(30deg);
  padding: 6px 30px 6px 50px;
  margin-left: -20px;
}

.Mission .name h1 {
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  -webkit-transform: skew(-30deg);
          transform: skew(-30deg);
}

.Mission .progress {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.7);
}
.Mission .progress .bar {
  border-radius: 2px;
  width: 95%;
  height: 5px;
  box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.4);
  background: -webkit-linear-gradient(#4a494b, #515051);
  background: linear-gradient(#4a494b, #515051);
  position: relative;
}

.Mission .progress .bar-complete {
  -webkit-transition: width 1s linear;
  transition: width 1s linear;
  border-radius: 2px;
  position: absolute;
  height: 5px;
  border: 1px solid rgba(0, 0, 0, 0.05);
  background: -webkit-linear-gradient(#a0a1a2, #b1b2b3);
  background: linear-gradient(#a0a1a2, #b1b2b3);
  max-width: 100%;
}

.Mission .bar .phase {
  text-transform: uppercase;
  position: absolute;
  width: 200px;
  margin-left: -100px;
  font-size: 11px;
}

.Mission .bar .phase.top {
  bottom: 10px;
}
.Mission .bar .phase.top:after {
  content: ' ';
  display: block;
  height: 8px;
  width: 8px;
  border-radius: 50%;
  box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.4);
  background: -webkit-linear-gradient(#a0a1a2, #b1b2b3);
  background: linear-gradient(#a0a1a2, #b1b2b3);
  position: absolute;
  right: 95px;
  top: 14px;
}

.Mission .bar .phase.bottom {
  top: 10px;
}
.Mission .bar .phase.bottom:before {
  content: ' ';
  display: block;
  height: 8px;
  width: 8px;
  border-radius: 50%;
  box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.4);
  background: -webkit-linear-gradient(#a0a1a2, #b1b2b3);
  background: linear-gradient(#a0a1a2, #b1b2b3);
  position: absolute;
  left: 95px;
  bottom: 14px;
}

